import React from 'react';
import { connect } from 'react-redux';
import { Formik, Form, Field } from 'formik';
import { editItem as editItemAction } from 'actions';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import withContext from 'hoc/withContext';
import UserPageTemplate from 'templates/UserPageTemplate';
import Input from 'components/atoms/Input/Input';
import ButtonIcon from 'components/atoms/ButtonIcon/ButtonIcon';
import Accept from 'assets/icons/iconmonstr-check-mark-2.svg';

const StyledWrapper = styled.div`
  padding: 25px 20px 25px 130px;
  max-width: 100vw;
  position: relative;

  @media(min-width: 620px) {
    max-width: 80vw;
    padding: 25px 50px 25px 130px;
  }
`;

const StyledForm = styled(Form)`
  display: flex;
  flex-direction: column;
`;

const StyledTextArea = styled(Input)`
  margin: 30px 0 20px;
  height: 30vh;
  border-radius: 20px;
`;

const StyledButtonIcon = styled(ButtonIcon)`
  background-color: ${({activecolor, theme}) => theme[activecolor]};
  border: 2px solid black;
  margin-left: auto;
`;

const DetailsTemplate = ({ id, pageContext, title, content, history, editItem }) => (
  <UserPageTemplate>
    <StyledWrapper>
      <Formik
        enableReinitialize
        initialValues={{ title: `${title}`, content: `${content}` }}
        onSubmit={( values ) => {
          editItem( id, pageContext, values );
          history.push(`/${pageContext}`);
        }}
      >
        {({ isSubmitting, values, handleChange, handleBlur}) => (
          <StyledForm>
            <Input as={Field} type="text" name="title" placeholder="Title" value={values.title}  />
            <StyledTextArea
              as="textarea"
              onChange={handleChange}
              onBlur={handleBlur}
              name="content"
              value={values.content}
              placeholder="Description"
            />
            <StyledButtonIcon icon={Accept} type="submit" activecolor={pageContext} />
          </StyledForm>
        )}
      </Formik>
    </StyledWrapper>
  </UserPageTemplate>
);

DetailsTemplate.propTypes = {
	pageContext: PropTypes.string.isRequired,
	title: PropTypes.string,
	content: PropTypes.string,
};

DetailsTemplate.defaultProps = {
 title: '',
 content: '',
};

const mapDispatchToProps = dispatch => ({
  editItem: ( id, itemType, itemContent ) => dispatch( editItemAction( id, itemType, itemContent ) ),
});

export default connect(
  null,
  mapDispatchToProps
)( withContext( DetailsTemplate ) );
